
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import vFooter from "@/components/v-footer";
const qs = require("qs");
import { Header } from "mint-ui";
export default {
data() {
return {
urlPic:this.api.urlPic,
totalPage: 1,
currentPage: 0,
listData:[],
isLevelPackage: this.getUrlParam("isLevelPackage")||1,
pullupEnabled: true,
loadMoreVal:true,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
}
}
},
components: {
"v-footer": vFooter,
Scroller,
Spinner,
Flexbox,
FlexboxItem
},
beforeCreate() {
document.title = "新银众商";
},
created() {
this.loadMore();
},
methods: {
loadMore (index) {
if(this.loadMoreVal){
this.loadMoreVal=false;
setTimeout(()=>{
this.loadMoreVal=true;
},1000)
}else{
return;
}
if(index==1){
this.currentPage = 0;this.totalPage = 1;this.listData=[];
if(this.currentPage< this.totalPage){
this.currentPage= parseInt(this.currentPage) + 1;
this.$axios.post(this.api.getGoodsList,qs.parse({ "page" : this.currentPage, "limit":8 ,"isLevelPackage":this.isLevelPackage }),{headers: {"content-type": "application/json"}})
.then(res => {
console.log(res.data);
this.totalPage=res.data.content.totalPage;
this.listData = [...this.listData,...res.data.content.list];
console.log(this.listData);
})
.catch(res => {

});
}else{
this.status.pullupStatus = 'complete';
setTimeout(()=>{
this.status.pullupStatus = 'default';
},1000);
console.log('已加载完');
}
setTimeout(() => {
this.$refs.scroller.donePullup()
}, 10)
}else{
if(this.currentPage< this.totalPage){
this.currentPage= parseInt(this.currentPage) + 1;
this.$axios.post(this.api.getGoodsList,qs.parse({ "page" : this.currentPage, "limit": 8,"isLevelPackage":this.isLevelPackage  }),{headers: {"content-type": "application/json"}})
.then(res => {
console.log(res.data);
this.totalPage=res.data.content.totalPage;
this.listData = [...this.listData,...res.data.content.list];
console.log(this.listData);
})
.catch(res => {

});
}else{
this.status.pullupStatus = 'complete';
setTimeout(()=>{
this.status.pullupStatus = 'default';
},1000);
console.log('已加载完');
}
setTimeout(() => {
this.$refs.scroller.donePullup()
}, 10)
}
},
refresh () {
console.log('刷新');
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 10)
})
},
toDetail (id) {
console.log(21);
this.$router.push({//核心语句
path:'/goods',//跳转的路径
query:{//路由传参时push和query搭配使用 ，作用时传递参数
id: id,
vGoods:'1'
}
})
}
}
};



